@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: 'Bree Serif', serif;
}

.PaperHead {
    width: 330px;
    text-align: center;
    margin: 10px;
    box-shadow: 2px 2px 5px black;
}

.Product-img {
    height: 250px;
    padding: 5px;
}

.Product-head {
    text-decoration: none;
    color: blue;
    font-weight: bolder;
}

.Product-list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-content: center;
            align-content: center;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center;
}

.container {
    margin-top: 20px;
    margin-left: 20px;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex !important;
    display: flex !important;
    margin: 0 auto !important;
    width: 100% !important;
}

.PaperHead1 {
    width: 100%;
    text-align: center;
    margin: 10px;
    box-shadow: 2px 2px 5px black;
}

.Product-img1 {
    height: 500px;
    padding: 5px;
}

.Product-head1 {
    text-decoration: none;
    color: black;
    font-weight: bold;
    font-size: 35px;
}

.head-one {
    background-color: #656fa7;
    color: white;
    margin-top: 0;
    text-align: center;
    font-weight: 500;
    font-size: 18px;
    padding: 5px;
}

.Header {
    font-size: 50px;
    text-align: center;
}

.Header-two {
    font-size: 18px;
    text-align: center;
    font-weight: lighter;
}

.Shoe-img {
    width: 100%;
}

.About-head {
    font-size: 40px;
    text-align: center;
    text-decoration: underline;
}

.About-para {
    font-size: 18px;
    margin-top: 35px !important;
    font-weight: lighter;
    text-align: justify;
}

.Pirce-tag {
    color: black;
    -webkit-text-decoration: black;
            text-decoration: black;
}

.Pirce-tag-in {
    color: black;
    text-decoration: none;
    font-size: 40px;
}

@media (min-width: 1280px) {
    .container {
        margin-top: 20px;
        margin-left: 20px;
        text-align: center;
        -webkit-align-items: center;
                align-items: center;
        display: -webkit-flex !important;
        display: flex !important;
        margin: 0 auto !important;
        max-width: 1098px !important;
    }
}
